"use client";

import React, { useState, useEffect } from "react";

export default function DogsCRUD() {
  const [dogs, setDogs] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [modalMode, setModalMode] = useState("create");
  const [currentDog, setCurrentDog] = useState(null);
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [dogToDelete, setDogToDelete] = useState(null);

  const initialFormState = {
    idDog: "",
    name: "",
    breed: "",
    birth_date: "",
    gender: "",
    owner: "",
    phone: "",
  };

  const [form, setForm] = useState(initialFormState);

  useEffect(() => {
    fetchDogs();
  }, []);

  const fetchDogs = async () => {
    try {
      const res = await fetch("http://localhost:3001/veterinary/dogs");
      const data = await res.json();
      setDogs(data);
    } catch (error) {
      console.error("Failed to fetch dogs:", error);
    }
  };

  const openCreateModal = () => {
    setForm(initialFormState);
    setModalMode("create");
    setShowModal(true);
  };

  const openEditModal = (dog) => {
    setForm({
      ...dog,
      birth_date: dog.birth_date?.split("T")[0],
    });
    setCurrentDog(dog);
    setModalMode("edit");
    setShowModal(true);
  };

  const openDeleteModal = (dog) => {
    setDogToDelete(dog);
    setShowDeleteModal(true);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!form.name || !form.breed || !form.birth_date) {
      alert("Please fill in name, breed and birth date.");
      return;
    }

    try {
      const url =
        modalMode === "create"
          ? "http://localhost:3001/veterinary/dog"
          : `http://localhost:3001/veterinary/dog/${currentDog._id}`;

      const method = modalMode === "create" ? "POST" : "PUT";

      await fetch(url, {
        method,
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(form),
      });

      fetchDogs();
      setShowModal(false);
    } catch (error) {
      console.error("Error saving dog:", error);
    }
  };

  const handleDelete = async () => {
    try {
      await fetch(`http://localhost:3001/veterinary/gos/${dogToDelete._id}`, {
        method: "DELETE",
      });
      fetchDogs();
      setShowDeleteModal(false);
    } catch (error) {
      console.error("Error deleting dog:", error);
    }
  };

  return (
    <>
    <div>
      <h2>Dogs CRUD</h2>
      <button onClick={openCreateModal}>Add Dog</button>

      <table border="1">
        <thead>
          <tr>
            <th>ID</th>
            <th>Name</th>
            <th>Breed</th>
            <th>Birth Date</th>
            <th>Human Age</th>
            <th>Dog Age</th>
            <th>Gender</th>
            <th>Owner</th>
            <th>Phone</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {dogs.length === 0 ? (
            <tr>
              <td colSpan={9}>No dogs found.</td>
            </tr>
          ) : (
            dogs.map((dog) => (
              <tr key={dog._id}>
                <td>{dog.idDog}</td>
                <td>{dog.name}</td>
                <td>{dog.breed}</td>
                <td>{dog.birth_date?.split("T")[0]}</td>
                <td>{dog.human_age}</td>
                <td>{dog.dog_age}</td>
                <td>{dog.gender}</td>
                <td>{dog.owner}</td>
                <td>{dog.phone}</td>
                <td>
                  <button onClick={() => openEditModal(dog)}>Edit</button>{" "}
                  <button onClick={() => openDeleteModal(dog)}>Delete</button>
                </td>
              </tr>
            ))
          )}
        </tbody>
      </table>

      {showModal && (
        <div className="modal" onClick={() => setShowModal(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <h3>{modalMode === "create" ? "Add Dog" : "Edit Dog"}</h3>
            <form onSubmit={handleSubmit}>
              <input name="idDog" value={form.idDog} onChange={handleChange} placeholder="IdDog" required/><br/>
              <input name="name" value={form.name} onChange={handleChange} placeholder="Name" required /><br />
              <input name="breed" value={form.breed} onChange={handleChange} placeholder="Breed" required /><br />
              <input type="date" name="birth_date" value={form.birth_date} onChange={handleChange} required /><br />
              <select name="gender" value={form.gender} onChange={handleChange}>
                <option value="">--Select Gender--</option>
                <option value="Male">Male</option>
                <option value="Female">Female</option>
              </select><br />
              <input name="owner" value={form.owner} onChange={handleChange} placeholder="Owner" /><br />
              <input name="phone" value={form.phone} onChange={handleChange} placeholder="Phone" /><br />
              <button type="submit">{modalMode === "create" ? "Add" : "Save"}</button>
              <button type="button" onClick={() => setShowModal(false)}>Cancel</button>
            </form>
          </div>
        </div>
      )}

      {showDeleteModal && (
        <div className="modal" onClick={() => setShowDeleteModal(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <p>Are you sure you want to delete <strong>{dogToDelete.name}</strong>?</p>
            <button onClick={handleDelete}>Yes, Delete</button>
            <button onClick={() => setShowDeleteModal(false)}>Cancel</button>
          </div>
        </div>
      )}
    </div>
    </>
  );
}
